.info {
  padding: 0 0 60px;
  margin-top: -30px;
  &__cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
  }
  &__card {
    background: #FFFFFF;
    box-shadow: 10px 40px 50px rgba(229, 233, 246, 0.4);
    border: 1px solid #ffffff;
    border-radius: 10px;
    padding: 30px 30px 15px;
    text-align: center;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &:hover {
      border: 1px solid #BBEAFE;
      box-shadow: 10px 0px 50px rgba(0, 145, 210, 0.19);
     .info__card-btn {
        background: linear-gradient(89.73deg, #008FD1 0.29%, #5DC6F6 99.83%);
        color: #ffffff;
      }
    }
    &-icon {
      width: 73px;
      height: 73px;
      border-radius: 50%;
      background: #FFFFFF;
      box-shadow: 4px 15px 50px rgba(229, 233, 246, 0.4);
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      top: -36px;
      right: 15px;
    }
    &-title {
      font-weight: 600;
      font-size: 24px;
      line-height: 120%;
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
      color: #35728E;
      margin-top: 12px;
      margin-bottom: 25px;
    }
    &-btn {
      width: 138px;
      height: 46px;
      background: #FFFFFF;
      border: 1px solid #2FABE4;
      border-radius: 32px;
      font-weight: 600;
      font-size: 16px;
      line-height: 120%;
      color: #35728E;
      margin: 0 auto;
      &2 {
        width: 215px;
      }
      &3 {
        width: 215px;
      }
    }
  }
}
.popular-services {
  padding: 60px 0;
  &__title {
    text-align: center;
  }
  &__cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    gap: 12px;
    margin-top: 50px;
  }
  &__card {
    background: #FFFFFF;
    border: 1px solid #ffffff;
    border-radius: 10px;
    filter: drop-shadow(10px 0px 50px rgba(229, 233, 246, 0.4));
