    &:hover {
      filter: drop-shadow(10px 0px 50px rgba(0, 145, 210, 0.19));
      border: 1px solid #bbeafe;
    }
    &-title {
      font-weight: 600;
      font-size: 24px;
      line-height: 120%;
      text-align: center;
    }
    &-img {
      margin-top: 20px;
      img {
        width: 100%;
        height: auto;
        border-radius: 10px;
      }
    }
  }
}
.directions {
  padding: 60px 0;
  position: relative;
  &__img-bg {
    position: absolute;
    z-index: 0;
    top: 130px;
    left: 0;
    pointer-events: none;
  }
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__title {
    text-align: center;
  }
  &__cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px 80px;
    margin-top: 50px;
  }
  &__card {
    text-align: center;
    background: #FFFFFF;
    box-shadow: 10px 40px 50px rgba(229, 233, 246, 0.4);
    border: 1px solid #ffffff;
    border-radius: 20px;
    padding: 20px;
    &:hover {
      border: 1px solid #BBEAFE;
      box-shadow: 10px 0px 50px rgba(0, 145, 210, 0.19);
    }
    &-img {
      img {
        width: 100%;
      }
    }
    &-title {
      font-weight: 600;
      font-size: 23px;
      line-height: 120%;
      margin-top: 25px;
    }
  }
}
.about {
  padding: 60px 0 85px;
  &__title {
    text-align: center;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    margin-top: 30px;
  }
  &__item {
    background: #FFFFFF;
    box-shadow: 10px 40px 50px rgba(229, 233, 246, 0.4);
    border-radius: 50%;
    width: 435px;
    height: 435px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
    &2 {
      position: relative;
      z-index: 2;
      margin-left: -53px;
    }
    &3 {
      position: relative;
      z-index: 3;
      margin-left: -53px;
    }
    &-number {
      font-weight: 500;
      font-size: 80px;
      line-height: 100%;
    }
    &-text {
      max-width: 260px;
      font-size: 18px;
      line-height: 120%;
      color: #35728E;
      margin-top: 12px;
    }
  }
}
.connection {
  padding: 85px 0 60px;
  position: relative;
  &__img-bg {
    position: absolute;
    z-index: 0;
    top: -350px;
    right: 0;
    pointer-events: none;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    z-index: 2;
  }
  &__left {
    max-width: 460px;
    flex-shrink: 0;
  }
  &__right {
    max-width: 560px;
    margin-left: 60px;
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__subtitle {
    font-size: 18px;
    line-height: 120%;
    margin-top: 5px;
    color: #1E1E1E;
  }
  &__form {
    margin-top: 50px;
    display: grid;
    grid-template-columns: 1fr;
    gap: 25px;
    &-group {
      padding: 0px;
      margin: 0;
      border: none;
      input {
        width: 100%;
        height: 60px;
        background: #FFFFFF;
        border: 1px solid #D7D7D7;
        box-shadow: 10px 40px 50px rgba(229, 233, 246, 0.4);
        border-radius: 10px;
        padding: 0 15px;
        font-size: 18px;
        line-height: 120%;
        &::placeholder {
          color: #1E1E1E;
          opacity: 0.4;
        }
      }
      textarea {
        width: 100%;
        height: 175px;
        background: #FFFFFF;
        border: 1px solid #D7D7D7;
        box-shadow: 10px 40px 50px rgba(229, 233, 246, 0.4);
        border-radius: 10px;
        padding: 18px 15px;
        font-size: 18px;
        line-height: 120%;
        &::placeholder {
          color: #1E1E1E;
          opacity: 0.4;
        }
      }
    }
    &-check {
      display: flex;
      align-items: center;
      position: relative;
      input {
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 2;
        opacity: 0;
        cursor: pointer;
        &:checked ~ label {
          &:after {
            background-image: url("../img/check.svg");
            background-repeat: no-repeat;
            background-position: center;
          }
        }
      }
      label {
        font-size: 14px;
        line-height: 120%;
        letter-spacing: -0.02em;
        padding-left: 36px;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 0;
          transform: translate(0, -50%);
          width: 26px;
          height: 26px;
          border: 1px solid #2B2B2B;
          background: transparent;
          border-radius: 4px;
        }
      }
    }
    &-link {
      font-size: 14px;
      line-height: 120%;
      letter-spacing: -0.02em;
      color: #008CC8;
      margin-top: 10px;
      display: block;
    }
  }
}
.specialists {
  padding: 60px 0;
  overflow: hidden;
  &__title {
    text-align: center;
  }
  &__subtitle {
    font-size: 16px;
    line-height: 120%;
    margin: 12px auto 0;
    text-align: center;
    max-width: 815px;
  }
  &__slider {
    position: relative;
    margin-top: 215px;
  }
  .swiper {
    overflow: unset;
    &-wrapper {
      align-items: stretch;
    }
    &-slide{
      opacity: 0;
      transition: all .3s;
      pointer-events: none;
      height: auto;
      min-height: auto;
      &-active,
      &-next,
      &-next+.swiper-slide{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  &__card {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    height: 100%;
    &:hover {
      .specialists__card-arrow {
        opacity: 1;
        pointer-events: all;
      }
    }
    &-top {
      background: #FFFFFF;
      box-shadow: 7px -14px 50px rgba(229, 233, 246, 0.4);
      border-radius: 5px 0 0 0;
      position: relative;
      padding: 15px 173px 15px 8px;
    }
    &-bottom {
      background: linear-gradient(90.69deg, #008FD1 0.45%, #5DC6F6 134.88%);
      border-radius: 0px 0px 6px 6px;
      min-height: 62px;
      padding: 7px 30px 7px 7px;
      position: relative;
    }
    &-img {
      position: absolute;
      right: 0;
      bottom: 3px;
    }
    &-info {
      display: flex;
      &:not(:first-child) {
        margin-top: 15px;
      }
      i {
        width: 24px;
        height: auto;
        flex-shrink: 0;
        img {
          width: 100%;
          height: auto;
        }
      }
      span {
        font-size: 14px;
        line-height: 120%;
        margin-left: 12px;
      }
    }
    &-name {
      font-weight: 600;
      font-size: 20px;
      line-height: 120%;
      color: #FFFFFF;
    }
    &-post {
      font-size: 14px;
      line-height: 120%;
      color: #ffffff;
      margin-top: 3px;
    }
    &-arrow {
      position: absolute;
      right: 5px;
      bottom: 10px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
  }
  .swiper-button-next {
    right: -90px;
    &:after {
      display: none;
    }
  }
  .swiper-button-prev {
    left: -90px;
    &:after {
      display: none;
    }
  }
  .arrow-inner {
    position: relative;
  }
  .arrow {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(180deg);
  }
  .arrow2 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
.reviews {
  padding: 60px 0;
  overflow: hidden;
  &__title {
    text-align: center;
  }
  &__slider {
    margin-top: 50px;
  }
  .swiper {
    overflow: unset;
    &-slide {
      background: #FFFFFF;
      border-radius: 1px;
      filter: drop-shadow(10px 40px 50px rgba(229, 233, 246, 0.4));
      opacity: 0.2;
      max-width: 785px;
      padding: 12px 20px 40px;
      &-active {
        opacity: 1;
      }
    }
  }
  &__card {
    &-top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1px;
        background: #35728E;
        opacity: 0.2;
      }
    }
    &-left {
      display: flex;
      align-items: center;
    }
    &-right {
      display: flex;
      align-items: center;
    }
    &-img {
      width: 60px;
      height: 60px;
      background-image: url("../img/reviews.webp");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      border-radius: 50%;
      font-weight: 700;
      font-size: 26.55px;
      line-height: 120%;
      color: #FFFFFF;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    &-name {
      margin-left: 15px;
      span:nth-child(1) {
        font-size: 18px;
        line-height: 120%;
        color: #212B36;
        display: block;
      }
      span:nth-child(2) {
        font-weight: 600;
        font-size: 14px;
        line-height: 120%;
        color: #637381;
        display: block;
      }
    }
    &-estimation {
      font-size: 18px;
      line-height: 120%;
      color: #35728E;
      span {
        font-weight: 600;
        color: #212B36;
      }
    }
    &-icon {
      margin-left: 15px;
    }
    &-desc {
      font-size: 16px;
      line-height: 150%;
      color: #637381;
      padding-top: 33px;
    }
  }
  &__link {
    width: 362px;
    height: 46px;
    background: #FFFFFF;
    border: 1px solid #2FABE4;
    border-radius: 32px;
    margin: 50px auto 0;
    background: linear-gradient(89.73deg, #008FD1 0.29%, #5DC6F6 99.83%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    color: #ffffff;
    font-size: 16px;
    line-height: 120%;
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover {
      background: linear-gradient(89.73deg, #008FD1 0.29%, #5DC6F6 99.83%);
      color: #ffffff;
      -webkit-background-clip: unset;
      -webkit-text-fill-color: unset;
      text-fill-color: unset
    }
  }
}
.news {
