  .reviews {
    .swiper-slide {
      max-width: 500px;
    }
    &__card {
      &-top {
        display: block;
      }
      &-right {
        margin-top: 25px;
      }
    }
  }
  .heading {
    &__left {
      flex-shrink: 0;
      max-width: 325px;
    }
    &__desc {
      margin-top: 25px;
      p {
        &:not(:first-child) {
          margin-top: 25px;
        }
      }
    }
  }
}

@media screen and (max-width: 850px) {
  .heading {
    &__inner {
      flex-direction: column;
    }
    &__left {
      max-width: 400px;
    }
    &__right {
      max-width: 100%;
      margin: 0 0 30px 0;
      order: -1;
    }
  }
}

@media screen and (max-width: 768px) {
  .info {
    padding: 0 0 30px;
    &__cards {
      gap: 30px;
    }
    &__card {
      &-icon {
        top: -25px;
        width: 50px;
        height: 50px;
        svg {
          width: 30px;
          height: auto;
        }
      }
      &-title {
        font-size: 22px;
      }
      &-btn {
        height: 40px;
      }
    }
  }
  .popular-services {
    padding: 30px 0;
    &__cards {
      grid-template-columns: 1fr 1fr;
      margin-top: 30px;
    }
    &__card {
      &-title {
        font-size: 20px;
      }
    }
  }
  .directions {
    padding: 30px 0;
    &__img-bg {
      display: none;
    }
    &__cards {
      margin-top: 30px;
    }
    &__card {
      &-title {
        font-size: 20px;
      }
    }
  }
  .about {
    padding: 30px 0;
    &__item {
      &-number {
        font-size: 50px;
      }
      &-text {
        font-size: 16px;
      }
    }
  }
  .connection {
    padding: 30px 0;
    &__img-bg {
      display: none;
    }
    &__inner {
      flex-direction: column;
      max-width: 450px;
      margin: 0 auto;
    }
    &__left {
      max-width: 100%;
    }
    &__right {
      max-width: 100%;
      margin: 30px 0 0 0;
    }
    &__form {
      gap: 15px;
      margin-top: 30px;
    }
  }
  .specialists {
    padding: 30px 0;
  }
  .reviews {
    padding: 30px 0;
    &__slider {
      margin-top: 30px;
    }
    .swiper-slide {
      max-width: 350px;
    }
    &__card {
      &-desc {
        font-size: 14px;
      }
    }
  }
  .news {
    padding: 30px 0;
    &__cards {
      margin-top: 30px;
      gap: 20px;
    }
    &__card {
      padding: 15px 10px;
      &-img {
        height: 180px;
      }
      &-desc {
        font-size: 16px;
      }
      &-link {
        margin-top: 10px;
      }
    }
  }
  .telegram {
    padding: 30px 0;
    &__inner {
      display: block;
      margin-top: 30px;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      margin: 30px 0 0 0;
    }
  }
  .heading {
    padding: 30px 0;
  }
  .contacts {
    padding: 30px 0;
  }
  .bg {
    background: unset;
    &2,
    &3,
    &4 {
      background: unset;
    }
  }
}

@media screen and (max-width: 576px) {
  .info {
    &__card {
      padding: 30px 15px 15px;
    }
  }
  .directions {
    &__cards {
      grid-template-columns: 1fr;
      gap: 15px;
    }
  }
  .about {
    &__item {
      width: 380px;
      height: 380px;
    }
  }
  .specialists {
    &__slider {
      margin-top: 100px;
    }
    &__card {
      &-top {
        padding: 15px 110px 15px 8px;
      }
      &-img {
        width: 100px;
      }
      &-name {
        font-size: 18px;
      }
    }
    .swiper-button-next {
      margin-left: 45px;
    }
    .arrow-border {
      width: 60px;
      img {
        width: 100%;
        height: auto;
      }
    }
  }
  .reviews {
    .swiper-slide {
      max-width: 100%;
    }
  }
  .news {
    &__cards {
      grid-template-columns: 1fr;
    }
    &__card {
      &-link {
        grid-column: 1;
        width: 100%;
      }
    }
  }
  .contacts {
    &__right {
      height: 400px;
    }
    &__tabs {
      display: block;
    }
    &__tab {
      width: 100%;
    }
  }
}

@media screen and (max-width: 450px) {
  .popular-services {
    &__cards {
      grid-template-columns: 1fr;
    }
  }
  .about {
    &__item {
      width: 280px;
      height: 280px;
      padding: 15px;
    }
  }
  .reviews {
    &__link {
      width: calc(100% - 30px);
      font-size: 14px;
      margin-top: 30px;
    }
  }
}