.footer {
  background: #BBEAFE;
  padding: 60px 0;
  &__top {
    padding-bottom: 35px;
  }
  &__bottom {
    padding-top: 17px;
    border-top: 1px solid rgba(53, 114, 142, 0.2);
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__items {
    display: flex;
    justify-content: space-between;
  }
  &__item {
    &:not(:first-child) {
      margin-left: 15px;
    }
  }
  &__logo {
    max-width: 308px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__socials {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 10px;
  }
  &__social {
    width: 27px;
    &:not(:first-child) {
      margin-left: 15px;
    }
    img {
      width: 100%;
      height: auto;
    }
  }
  &__list {
    li {
      font-size: 16px;
      line-height: 120%;
      color: #35728E;
      &:not(:first-child) {
        margin-top: 15px;
      }
      &:hover {
        color: #008DD2;
      }
    }
  }
  &__btn {
    width: 224px;
    height: 49px;
    background: linear-gradient(180deg, #107FC9 0%, #50B5F8 100%);
    border-radius: 50px;
    font-size: 20px;
    line-height: 120%;
    color: #ffffff;
  }
  &__numbers {
    margin-top: 13px;
    text-align: center;
  }
  &__number {
    font-weight: 700;
    font-size: 20px;
    line-height: 120%;
    color: #35728E;
    display: block;
    &:not(:first-child) {
      margin-top: 10px;
    }
  }
  &__politics {
    font-size: 18px;
    line-height: 120%;
    color: #35728E;
  }
  &__location {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #35728E;
    span {
      font-size: 18px;
      line-height: 120%;
      color: #35728E;
      margin-left: 5px;
    }
  }
  &__brevis {
    display: flex;
    &-desc {
      margin-left: 8px;
      font-size: 14px;
      line-height: 120%;
      color: #35728E;
      margin-top: 10px;
      span {
        display: block;
        text-decoration-line: underline;
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .footer {
    &__logo {
      max-width: 270px;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    padding: 30px 0;
    &__inner {
      padding: 0 15px;
    }
    &__bottom {
      justify-content: start;
      align-items: start;
      flex-direction: column;
    }
    &__items {
      display: block;
    }
    &__item {
      &:not(:first-child) {
        margin: 30px 0 0 0;
      }
    }
    &__socials {
      justify-content: start;
    }
    &__numbers {
      text-align: start;
    }
    &__location {
      display: inline-block;
      margin: 30px 0;
    }
  }
}